<template>
  <div class="padding">
    <transition name="page" mode="out-in">
      <div :key="$route.params.blockIndex">
        <button @click="backToPage" class="delete is-medium is-pulled-right"></button>

        <strong class="is-size-5">
          Edit block
        </strong>    

        <div>{{ block.name }}</div>
        <hr>

        <div v-for="field in block.fields" class="field">
          <component :is="field.type + '-field'" :field="field" :level="0" :options="options"></component>
        </div>

      </div>
    </transition>
  </div>
</template>

<script>
import TextField from '../components/TextField'
import TextareaField from '../components/TextareaField'
import CollectionField from '../components/CollectionField'

export default {
  name: 'block',

  data () {
    return {
      block: {},
      options: {
        activeLevel: 0,
      }
    }
  },

  created() {
    this.block = data.pages[this.$route.params.pageIndex].blocks[this.$route.params.blockIndex];
    // iframe.goToPage(this.$route.params.pageIndex);
  },

  beforeRouteUpdate (to, from, next) {
    this.block = data.pages[to.params.pageIndex].blocks[to.params.blockIndex];
    // iframe.goToPage(to.params.pageIndex);
    next();
  },


  methods: {
    backToPage: function () {
      this.$router.push('/websites/1/build/pages/' + this.$route.params.pageIndex);
    }
  },

  components: {
    'text-field': TextField,
    'textarea-field': TextareaField,
    'collection-field': CollectionField,
  }
}
</script>

<style lang="scss" scoped>
  .padding {
    padding: 10px 30px;
  }
</style>



