<template>
	<div>
    <div v-show="options.activeLevel == level">
    
  		<label class="label">{{ field.label }}</label>

      <nav class="panel">
        <a v-for="(item, index) in field.value" class="panel-block" :href="index">
          <span class="panel-icon">
            <i class="fa fa-book"></i>
          </span>

          {{ item[Object.keys(item)[0]].value }} 
        </a>
      </nav>

      <a class="button is-info is-small add-item" @click="addItem">
        Add item
      </a>
    </div>

    <div v-show="options.activeLevel == level + 1" >
      <div v-for="field in field.fields" class="field">
        <component :level="level + 1" :is="field.type + '-field'" :field="field" :options="options"></component>
      </div>

    
      <a  class="button is-info is-small add-item">
        Save item
      </a>
      <a @click="cancel" class="button is-info is-small add-item">
        Cancel
      </a>
    </div>
  </div>
</template>

<script>
import TextField from '../components/TextField'
import TextareaField from '../components/TextareaField'
import CollectionField from '../components/CollectionField'

export default {
  props: ['field', 'level', 'options'],
  
  data () {
    return {
    }
  },

  methods: {
    addItem: function () {
      this.options.activeLevel++;
    },

    cancel: function () {
      this.options.activeLevel--;
    }
  },

  components: {
    'text-field': TextField,
    'textarea-field': TextareaField,
    'collection-field': CollectionField,
  }
}
</script>

<style>
  .panel {
    margin-bottom: 10px !important;
  }
</style>



