<template>
	<div v-show="options.activeLevel == level">
		<label class="label">{{ field.label }}</label>
	  <div class="control">
	  	<textarea v-model="field.value" rows="2" class="textarea" :placeholder="field.label"></textarea>
	  </div>
  </div>
</template>

<script>
export default {
  props: ['field', 'level', 'options'],
}
</script>

<style>
</style>



