<template>
	<div v-show="options.activeLevel == level">
    <label class="label">{{ field.label }}</label>
	  <div class="control">
	    <input v-model="field.value" class="input" type="text" :placeholder="field.label">
	  </div>
  </div>
</template>

<script>
export default {
  props: ['field', 'level', 'options'],
}
</script>

<style>

</style>



